<template>
  <div @click="hide">
    <!--  steps步骤条-->
    <!-- 头部 -->
    <van-row type="flex" justify="space-between" align="center" class="top">
      <van-col span="6" class="l">
        <van-icon @click="back" name="arrow-left" />
      </van-col>
      <van-col span="8" class="c">购物车</van-col>

      <van-col span="6" class="r">
        <van-icon @click.stop="show" name="qr" v-if="show"></van-icon>
      </van-col>
      <van-grid class="dao" direction="vertical" :column-num="2" v-show="showdao">
        <van-grid-item
          v-for="item in menu"
          :icon="item.icon"
          :key="item.text"
          :to="item.path"
          :text="item.text"
        ></van-grid-item>
      </van-grid>
    </van-row>

    <van-steps :active="active">
      <van-step>购物车</van-step>
      <van-step>提交订单</van-step>
      <van-step>付款</van-step>
      <van-step>购买完成</van-step>
    </van-steps>
    <!-- 卡片 --渲染商品卡片 -->
    <van-card
      :price="item.sales_price"
      :title="item.goods_name"
      :thumb="item.img_url"
      v-for="item in goodslist"
      :key="item._id"
      @click-thumb="gotoDetail(item._id)"
    >
      <!--只点击图片跳转 -->
      <template #tag>
        <van-checkbox v-model="item.checked"></van-checkbox>
      </template>
      <template #price>
        <p class="price">
          <del>{{item.price}}</del>
          <span>{{item.sales_price}}</span>
          <van-stepper
            v-model="item.qty"
            theme="round"
            input-width="20px"
            button-size="20px"
            async-change
            integer
            @change="changeQty(item._id,$event)"
          />
          <!-- $event保留原来的参数 -->
        </p>
      </template>
      <template #footer>
        <van-button plain type="danger" icon="cross" @click.stop="removeItem(item._id)" size="mini"></van-button>
      </template>
    </van-card>
    <div style="padding:10px">
      <van-button plain type="danger" size="small" @click="clearCart">清空购物车</van-button>
    </div>
    <van-submit-bar :price="totalPrice" button-text="提交订单" @submit="onSubmit">
      <van-checkbox v-model="checkAll">全选</van-checkbox>
      <template #tip>
        你的收货地址不支持同城送,
        <span>修改地址</span>
      </template>
    </van-submit-bar>
  </div>
</template>
<script>
//单个文件引入==》性能更好
import Vue from "vue";
import {
  GridItem,
  Grid,
  Icon,
  Row,
  Col,
  Card,
  Step,
  Steps,
  Button,
  SubmitBar,
  Stepper,
} from "vant";
import { mapState, mapMutations, mapActions } from "vuex";
Vue.use(Card);
Vue.use(Step);
Vue.use(Steps);
Vue.use(Button);
Vue.use(SubmitBar);
Vue.use(Stepper);
Vue.use(Col);
Vue.use(Row);
Vue.use(Icon);
Vue.use(Grid);
Vue.use(GridItem);
export default {
  name: "Cart",
  data() {
    return {
      active: 0,
      selecteds: [],
      showdao: false,
      menu: [
        {
          name: "home",
          path: "/home",
          icon: "wap-home-o",
          text: "首页",
        },
        {
          name: "discover",
          path: "/discover",
          icon: "smile-o",
          text: "分类",
        },
        {
          name: "cart",
          path: "/cart",
          icon: "shopping-cart-o",
          text: "购物车",
        },
        {
          name: "mine",
          path: "/mine",
          icon: "user-o",
          text: "个人中心",
        },
      ],
    };
  },
  computed: {
    // goodslist() {
    //   return this.$store.state.cart.goodslist;
    // },
    //下面是简写的写法
    //把vuex中state数据映射到组件的computed
    // ...mapState(["goodslist"]), //state中的方法名和这边的函数一样时可以这样写,等效于：goodslist()=>this.$store.state.goodslist
    //不同的时候   // 等效于：cartlist()=>this.$store.state.goodslist
    ...mapState({
      // { cartlist: "goodslist" }
      // 如果有命名空间的话
      // 获取存储器里属于购物车的数据
      goodslist(state) {
        return state.cart.goodslist;
      },
    }),
    checkAll: {
      get() {
        return this.goodslist.every((item) => item.checked);
      },
      set(val) {
        console.log("复选框", val);
        this.goodslist = this.goodslist.map((item) => {
          item.checked = val;
          return item;
        });
      },
    },
    totalPrice() {
      //没有store的写法
      // return this.goodslist.reduce((pre,item)=>pre+item.sales_price*item.qty,0)*100;
      return this.$store.getters.totalPrice;
      //添加命名空间的获取方式
      // return this.$store.getters['cart/totalPrice']
    },
  },
  methods: {
    back() {
      window.history.go(-1);
    },
    show() {
      this.showdao = true;
      console.log(555);
    },
    hide() {
      this.showdao = false;
      console.log(66);
    },
    onSubmit() {
      console.log(123);
    },
    gotoDetail(id) {
      //去商品详情
      this.$router.push("/goods/" + id);
    },
    //移除
    async removeItem(id) {
      this.$store.commit("remove", id);
      console.log(123);
      //删除数据库的数据
      const data=await this.$request.delete(`/goods/`+id)
      console.log(data);
    },
    //移除和清空
    ...mapMutations({
      // removeItem: "remove",
      clearCart: "clear",
    }),
    //清空购物车
    // clearCart() {
    //   console.log(333);
    //   this.$store.commit("clear");
    // },
    //修改数量
    // changQty(id, qty) {
    //   this.$store.commit("changeQty", { _id: id, qty });
    //   this.$store.dispatch('changeQtyAsync',{_id:id,qty})
    // },
    //修改数量
    ...mapActions({
      // changeQty:'changeQtyAsync'
      changeQty(dispatch, _id, qty) {
        dispatch("changeQtyAsync", { _id, qty });
      },
    }),
  },
  created() {
    // this.$parent.showMenu = false;
    // console.log("goodslist=", this.$store.state);
    //隐藏下边的东西
    //  this.$store.dispatch("getCart");
    this.$store.commit("displayTabbar", false);
  },
  beforeDestroy() {
    // this.$parent.showMenu = true;
    // console.log("cart.destroyed", this.$parent.showMenu);
    //显示下边的东西
    this.$store.commit("displayTabbar", true);
  },
   beforeRouteUpdate(to, from, next) {
    if (to.params.id !== from.params.id) {
          this.$store.dispatch("getCart");

    }
    next();
  },
};
</script>
<style lang="scss" scope>
.van-image{
  display: flex;
}
.van-image__img{
  margin: auto;
}
.top {
  position: relative;
  height: 58px;
  background-color: #000;
  background-image: -webkit-linear-gradient(#323232, #414141);
  .l,
  .c,
  .r {
    font-size: 25px !important;
    font-weight: 400px;
    color: white;
  }
  .c {
    text-align: center;
    // background-color: green;
    // line-height: 31px;
  }
  .l {
    //     padding-left: 5px;
    //     //   background-color: yellow;
    //     padding-top: 4px;
    text-align: center;
  }
  .r {
    //     padding-left: 65px;
    //     //   background-color: red;
    //     padding-top: 4px;
    //     position: relative;
    text-align: center;
  }
  .dao {
    position: absolute;
    width: 180px;
    height: 200px;
    right: 0px;
    top: 58px;
    z-index: 9999;
    // background: red;
    .van-grid-item__content {
      background: #000;
      color: white;
      background-image: -webkit-linear-gradient(#323232, #414141);
      .van-grid-item__text {
        color: white;
      }
    }
  }
}
</style>